import { ShoppingBag } from '@mui/icons-material';
import { Divider, Drawer, List, ListItem, ListItemText, Typography } from '@mui/material';
import React from 'react';
import BasketItem from './BasketItem';
import { useDispatch, useSelector } from 'react-redux';
import { setIsOpen } from './store/basketSlice';

const Basket = () => {

   const dispatch = useDispatch();
   const { selectedProducts, isOpen } = useSelector(state => state.basket);

   return (
      <Drawer
         anchor='right'
         open={isOpen}
         onClose={() => dispatch(setIsOpen(false))}
         sx={{ margin: '0 auto' }}
      >
         <List sx={{ width: '300px' }}>
            <ListItem>
               <ShoppingBag sx={{ marginRight: '20px' }} />
               <ListItemText primary='' />
            </ListItem>

            <Divider />

            {selectedProducts.length ? (
               <>
                  {selectedProducts.map(item => (
                     <BasketItem key={item.id} {...item} />
                  ))}
                  <Divider />
                  <ListItem>
                     <Typography
                        variant='h6'
                        component='span'
                     >
                          {selectedProducts.reduce((acc, item) => (
                           acc + item.price
                        ), 0)} .
                     </Typography>
                  </ListItem>
               </>
            ) : (
               <ListItem>
                   
               </ListItem>
            )}
         </List>
      </Drawer>
   );
};

export default Basket;